#detailed {
    position: fixed;
    margin: 0;
    top: 30%;
    right: -30%;
    background-color: black;
    z-index: 3;
}

#detailed.active_info {
    right: 26%;
    -webkit-transition: all 400ms linear;
    -o-transition: all 400ms linear;
    transition: all 400ms linear;
}



